import { LayoutDefaultComponent } from './layout/layout-default/layout-default.component';
import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
  {
    path: '',
    component: LayoutDefaultComponent,
    children: [
      { path: '', loadChildren: () => import('./views/home/home.module').then(mod => mod.HomeModule)},
      { path: 'category', loadChildren: () => import('./views/category/category.module').then(mod => mod.CategoryModule)},
      { path: 'archive', loadChildren: () => import('./views/archive/archive.module').then(mod => mod.ArchiveModule)},
      { path: 'article', loadChildren: () => import('./views/article/article.module').then(mod => mod.ArticleModule)},
    ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
